<template>
  <div class="search">
    <div class="search-head">
      <NavBar>
        <van-icon name="arrow-left" slot="left" size="20" color="#fff" @click="$router.go(-1)"/>
        <div slot="title" class="title">搜索</div>
      </NavBar>
      <van-search
        v-model="value"
        show-action
        :placeholder="placeholder"
        @search="onSearch"
        @input="onInput"
        @clear="clear"
      >
        <template #action v-if="showList">
          <div @click="onSearch(value)">搜索</div>
        </template>
        <template #action v-else>
          <van-icon name="shopping-cart-o"
            size="20px"
            :badge="badge"
            @click="$router.push('/shopping')"
            v-if="!show"
          />
          <van-icon name="shopping-cart-o" size="20px" v-else/>
        </template>
      </van-search>
    </div>
    <div class="search-list" v-if="likeList.length && showList">
      <van-list>
        <van-cell v-for="item in likeList" :key="item" @click="onSearch(item)">
          <template>
            <span class="like-result" v-html="formatHTML(item)"></span>
          </template>
        </van-cell>
      </van-list>
    </div>
    <div class="goods-list" v-if="!showList">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        :immediate-check="false"
        @load="onLoad"
      >
        <GoodsCard
          v-for="(item, index) in GoodsList"
          :key="index"
          v-bind="item"
          :count="counterMap[item.id]"
        >
        </GoodsCard>
      </van-list>
    </div>
    <div class="history" v-if="likeList.length <=0 && showList">
      <MyHistory :searchList="searchList"></MyHistory>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import NavBar from '@/components/Navbar.vue';
import GoodsCard from '@/components/GoodsCard.vue';
import MyHistory from '@/components/MyHistory.vue';
import api from '@/api/index';

export default {
  data() {
    return {
      placeholder: '请输入搜索关键词',
      value: this.placeholder,
      timer: null,
      likeList: [],
      loading: false,
      finished: false,
      page: 1,
      size: 5,
      GoodsList: [],
      showList: true,
      total: 0,
      searchList: [],
    };
  },
  computed: {
    ...mapGetters(['counterMap']),
    badge() {
      const count = Object.values(this.counterMap).reduce((prev, next) => prev + next, 0);
      if (count > 99) {
        return '99+';
      }
      return count;
    },
    show() {
      const count = Object.values(this.counterMap).reduce((prev, next) => prev + next, 0);
      if (count > 0) {
        return false;
      }
      return true;
    },
  },
  watch: {
    value() {
      if (this.value === '') {
        this.showList = true;
      }
    },
  },
  components: {
    NavBar,
    GoodsCard,
    MyHistory,
  },
  methods: {
    clear() {
      this.showList = true;
    },
    formatHTML(item) {
      const reg = new RegExp(this.value, 'g');
      return item.replace(reg, this.value.fontcolor('red'));
    },
    onSearch(item) {
      if (item) {
        this.value = item;
      }
      const result = this.searchList.find((ele) => ele.value === this.value);
      if (result) {
        result.time = new Date().getTime();
        this.searchList.sort((a, b) => b.time - a.time);
      } else {
        this.searchList.unshift({ value: this.value, time: new Date().getTime() });
        if (this.searchList.length > 10) {
          this.searchList.pop();
        }
      }
      localStorage.setItem('searchList', JSON.stringify(this.searchList));
      this.likeList = [];
      this.page = 1;
      this.finished = false;
      this.onLoad();
      this.showList = false;
    },
    onInput() {
      if (this.value === '') {
        this.likeList = [];
        return;
      }
      if (this.timer) {
        clearTimeout(this.timer);
        this.timer = null;
      } else {
        this.timer = setTimeout(async () => {
          const res = await api.likeSearch(this.value);
          this.likeList = res.result;
          clearTimeout(this.timer);
          this.timer = null;
        }, 800);
      }
    },
    async onLoad() {
      const val = await api.search(this.value, this.page, this.size);
      this.GoodsList = [...this.GoodsList, ...val.list];
      this.total = val.total;
      this.loading = false;
      if (this.GoodsList.length > this.total) {
        this.finished = true;
      } else {
        this.page += 1;
      }
    },
  },
  created() {
    this.searchList = JSON.parse(localStorage.getItem('searchList')) || [];
  },
};
</script>

<style lang="less" scoped>
.search {
  width: 100%;
  height: 100vh;
  .search-head {
    .title {
      color: #fff;
      font-size: 14px;
    }
  }
  .search-list {
    width: 100%;
    position: relative;
    top: 0;
    padding-left: 16px;
  }
  .goods-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .history {
    margin-top: 10px;
    margin-left: 20px;
  }
}
</style>
